import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
export const routes = [
  {
    path: '/',
    components: {
      default: () => import('@/views/Index'),
    },
    meta: {
      showHeader: true,
      showFooter: true,
    },
  },
  {
    path: '/Introduce',
    components: {
      default: () => import('@/views/Introduce'),
    },
    meta: {
      showHeader: true,
      showFooter: true,
    },
  },
  {
    path: '/Info',
    components: {
      default: () => import('@/views/Info'),
    },
    meta: {
      showHeader: true,
      showFooter: true,
    },
  },
  {
    path: '/Recruit',
    components: {
      default: () => import('@/views/Recruit'),
    },
    meta: {
      showHeader: true,
      showFooter: true,
    },
  },
  {
    path: '/Cooperation',
    components: {
      default: () => import('@/views/Cooperation'),
    },
    meta: {
      showHeader: true,
      showFooter: true,
    },
  },
  {
    path: '/Message',
    components: {
      default: () => import('@/views/Message'),
    },
    meta: {
      showHeader: true,
      showFooter: true,
    },
  },
  {
    path: '/OnlineRegistration',
    components: {
      default: () => import('@/views/OnlineRegistration'),
    },
    meta: {
      showHeader: false,
      showFooter: true,
    },
  },
]

const router = new VueRouter({
  base:'/dist_education',
  mode: 'hash',
  routes
})

export default router
